<script setup>
const props = defineProps({
  modelValue: String
})

const emit = defineEmits(["update:modelValue", "input"])
const input = ref(null)
watch(
  () => props.modelValue,
  () => {
    console.log("modelValue changed")
  }
)

onMounted(() => {
  input.value.addEventListener("input", (e) => {
    console.log("input")
    // value.value = e.target.value
  })
})
defineExpose({
  input
})

// v-model props input watch watch input  dom input    el-input el-input  dom input  mounted

// el-input ui
const value = computed({
  get() {
    return props.modelValue
  },
  set(value) {
    //     input set   
    console.log(props.modelValue, value)
    //    input  watch 

    //  v-model target+
    // if (props.modelValue === value) return
    console.log(value + "vvvv")

    emit("update:modelValue", value)
    emit("input")
  }
})
function testInput1(e) {
  console.log("vvvv")
}
</script>

<template>
  <div ref="input">
    <input type="text" v-model="value" @input="testInput1" />
  </div>
</template>
